import React, { Component } from "react";
import ModelDownload from "../ModelDownload"
import axios from 'axios';

class Table extends Component {
  downloadModel(name) {
    axios({
      url: `https://raw.githubusercontent.com/TruSource/${name}/master/server/model.json`,
      method: 'GET',
      responseType: 'blob',
    }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'model.json');
      document.body.appendChild(link);
      link.click();
    });
  }

  render() {
    const { items, onSort } = this.props;
    return (
      <table className="table align-items-center">
        <thead>
          <tr>
            <th scope="col" onClick={() => onSort("name")}>
              Name
        </th>
            <th scope="col" onClick={() => onSort("category")}>
              Category
        </th>
            <th scope="col">Model</th>
            <th scope="col">Description</th>
          </tr>
        </thead>
        <tbody className="list">
          {items.map((item, index) => (
            <>
              <tr key={index}>
                <th scope="row" className="left-pill">
                  <a href={"https://docs.trusource.io/" + item.name}>{item.name}</a>
                </th>
                <th>{item.category}</th>
                <th align="center"><ModelDownload name={item.name} downloadModel={this.downloadModel} /></th>
                <th className="right-pill">{item.description}</th>
              </tr>
              <tr className="table-divider"></tr>
            </>
          ))}
        </tbody>
      </table>
    )
  }
};

export default Table;
